<template>
    <div>
        <v-slider :slideArr = "sliderArr"></v-slider>
        <div class="container">
            <section class="row">
                <div class="col-xs-12">
                    <div class="brand-wrapper clearfix">
                        <div class="brands-list col-xs-6">
                            <div class="brands" @mouseover="showAllbrand" @mouseout="hideAllbrand">
                                <div class="title">品牌<span class="glyphicon glyphicon-menu-down"></span></div>
                                <ul class="brand-list clearfix" v-if="brandArr.length" >
                                    <li class="col-xs-2" v-for="(item,index) in brandArr" :key="index" :data-id="item.id" @click=" getcarList(item.id)">
                                        <div class="brand-img">
                                            <img :src="item.brandIcon" alt="">
                                        </div>                                 
                                        <p class="brand-text">{{item.brand}}</p>
                                    </li>
                                </ul>
                            </div>                            
                        </div>
                         <div class="prices col-xs-6">
                            <div class="title">价格</div>
                            <ul class="price-list clearfix">
                                <li class="col-xs-4"><a @click="getcarListbyprice('3')">3万以内</a></li>
                                <li class="col-xs-4"><a @click="getcarListbyprice('3-5')">3-5万</a></li>
                                <li class="col-xs-4"><a @click="getcarListbyprice('5-8')">5-8万</a></li>
                                <li class="col-xs-4"><a @click="getcarListbyprice('8-10')">8-10万</a></li>
                                <li class="col-xs-4"><a @click="getcarListbyprice('10-20')">10-20万</a></li>
                                <li class="col-xs-4"><a @click="getcarListbyprice('20-30')">20-30万</a></li>
                                <li class="col-xs-4"><a @click="getcarListbyprice('30-50')">30-50万</a></li>
                                <li class="col-xs-4"><a @click="getcarListbyprice('50-80')">50-80万</a></li>
                                <li class="col-xs-4"><a @click="getcarListbyprice('100')">100万以上</a></li> 
                            </ul>
                        </div>
                    </div>                   
                </div>
            </section>
             <v-list :productArr = "leaseList" :type = "1">
                <div class="col-xs-12 clearfix product-title">
                    <div class="pull-left car-style">惠搜车</div>
                    <div class="pull-right">
                        <a @click="setUrl('leaseCar',1)" class="more">更多<span class="glyphicon glyphicon-menu-right"></span></a>
                    </div>
                </div>
                <div class="col-xs-12 product-banner" v-if="leaseCarImage">
                    <a :href="leaseCarImage.realUrl"><img :src="leaseCarImage.imgUrl" alt="" class="img-responsive"></a>                   
                </div>
            </v-list>
            <v-list :productArr = "importList" :type = "2">
                <div class="col-xs-12 clearfix product-title">
                    <div class="pull-left car-style">平行进口</div>
                    <div class="pull-right">
                        <a @click="setUrl('importCar',2)"  class="more">更多<span class="glyphicon glyphicon-menu-right"></span></a>
                    </div>
                </div>
                <div class="col-xs-12 product-banner" v-if="importCarImage">
                    <a :href="importCarImage.realUrl"><img :src="importCarImage.imgUrl" alt=""  class="img-responsive"></a>   
                </div>
            </v-list>
            <v-list :productArr = "secondHandList" :type = "3">
                <div class="col-xs-12 clearfix product-title">
                    <div class="pull-left car-style">会淘车</div>
                    <div class="pull-right">
                        <a @click="setUrl('secondHandCar',3)" class="more">更多<span class="glyphicon glyphicon-menu-right"></span></a>
                    </div>
                </div>
                <div class="col-xs-12 product-banner" v-if="secondHandCarImage">
                    <a :href="secondHandCarImage.realUrl"><img :src="secondHandCarImage.imgUrl" alt=""  class="img-responsive"></a>                   
                </div>
            </v-list>
            <section class="row">
                <div class="col-xs-12 clearfix product-title">
                    <div class="pull-left car-style">惠搜问答</div>
                    <div class="pull-right">
                        <router-link to='/questionList' class="more">更多<span class="glyphicon glyphicon-menu-right"></span></router-link>
                    </div>
                </div>
                <div class="col-xs-12 questions">
                    <ul class="questions-list" v-if="questionAnswerList.length != 0">
                        <li class='question' v-for="(item,index) in questionAnswerList" :key="index" :data-id = 'item.id'>
                            <h4>{{item.question}}</h4>
                            <p>{{item.answer}}</p>
                        </li>
                    </ul>
                </div>
            </section>
            <section class="row">
                <div class="col-xs-12 clearfix product-title">
                    <div class="pull-left car-style">车主故事</div>
                    <div class="pull-right">
                        <router-link to='/stories' class="more">更多<span class="glyphicon glyphicon-menu-right"></span></router-link>
                    </div>
                </div>
                <div class="col-xs-12 story">
                    <ul class="story-list clearfix" v-if="storyList.length != 0">
                        <li class="story col-xs-12" v-for="(item,index) in storyList" :key="index">
                            <div class="col-xs-2">
                                <img :src="item.icon" alt="" class="img-responsive">
                            </div>
                            <div class="col-xs-10">
                                <div class="story-title">
                                    {{item.title}}
                                </div>
                                <div class="content"></div>
                                <div class="time"><span>{{item.date}}</span><a class="more pull-right" @click="getstoryDetail(item.id,item.url)">查看更多</a></div>
                            </div>
                        </li>
                    </ul>
                </div>
            </section>
        </div> 
        <v-collector></v-collector>
    </div>
</template>

<script>
import vSlider from "@/components/pc/slider";
import vList from "@/components/pc/index-list";
import vCollector from "@/components/pc/collectmsg";
import {PC_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data:function(){
        return {
            sliderArr:[],
            brandArr:[],
            importList:[],
            secondHandList:[],
            leaseList:[],
            importCarImage:{},
            leaseCarImage:{},
            secondHandCarImage:{},
            questionAnswerList:[],
            storyList:[]
        }
    },
    components:{
        vSlider,
        vList,
        vCollector
    },
    methods:{
        computeData:function(data){
            this.sliderArr = data.carouselModels;
            this.brandArr = data.brandModels;
            this.importList = data.importList;
            this.secondHandList = data.secondList;
            this.leaseList = data.leaseList;
            this.secondHandCarImage = data.secondHandCarImage;
            this.importCarImage = data.importCarImage;
            this. leaseCarImage = data. leaseCarImage;
            this.questionAnswerList = data.questionAnswerList;
            this.storyList = data.storyList;
        },
        getindexData:function(){
            let _this = this;
            $.ajax({
                url:PC_COMMONURL+"/index",
                type:"get",
                data:{'platForm':'020003'},
                asnyc:true,
                dataType:"json",
                success:function(data){
                    if(data.code == SUCCESS_CODE){ 
                        console.log(data);            
                        _this.computeData(data.data);
                    }
                }
            })
        },
        getcarList:function(id) {
            this.$router.push({
                name: 'leaseCar',
                params: {
                    id: id
                }
            })
            $(".nav li").removeClass("active");
            $(".nav li").eq(1).addClass("active");
        },
        getcarListbyprice:function(price) {
            this.$router.push({
                name: 'leaseCar',
                params: {
                    price: price
                }
            })
            $(".nav li").removeClass("active");
            $(".nav li").eq(1).addClass("active");
        },
        setUrl:function(url,index){
            this.$router.push({
                name: url
            })
            $(".nav li").removeClass("active");
            $(".nav li").eq(index).addClass("active");
        },
        showAllbrand:function(){
            $(".brands").addClass("hover");
        },
        hideAllbrand:function(){
            if($(".brands").hasClass("hover")){
                $(".brands").removeClass("hover")
            } 
        },
        getstoryDetail(id,url) {
            this.$router.push({
                name: 'storyDesc',
                params: {
                id: id,
                url:url
                }
            })
        }
    },
    mounted:function(){
        this.getindexData();
    }
}
</script>



