<template>
    <div class="collector">
        <a  class="img-wrapper" @click="showPanel"><img src="static/images/question.png" alt=""></a>
        <div class="panel panel-primary" v-show = "isShowPanel">
            <!-- Default panel contents -->
            <div class="panel-heading">
                请您留言
                <button type="button" class="close pull-right" data-dismiss="alert" aria-label="Close" @click="showPanel"><span aria-hidden="true">&times;</span></button>
            </div>
            <div class="panel-body">
                <textarea name="text" id="mark" rows="3" placeholder="请在此留言" class="form-control"></textarea>
                <div class="input-group">
                    <span class="input-group-addon"><i class="glyphicon glyphicon-user"></i></span>
                    <input type="text" class="form-control" placeholder="姓名" id="name">
                </div>
                <div class="input-group">
                    <span class="input-group-addon"><i class="glyphicon glyphicon-earphone" ></i></span>
                    <input type="text" class="form-control" placeholder="电话（必填）" id="phone">
                </div>
                <button type="button" class="btn btn-primary" @click="submitMsg">提交</button>
            </div>
        </div>
    </div>
</template>

<script>
import {PC_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data:function(){
        return{
            isShowPanel:false
        }
    },
    methods:{
        showPanel:function(){
            if(this.isShowPanel){
                this.isShowPanel = false;
            }else{
                this.isShowPanel = true;
            }
        },
        submitMsg:function(){
            let mark = $("#mark").val();
            let name = $("#name").val();
            let phone = $("#phone").val();
            let data = {};
            data.mark = mark;
            data.name = name;
            data.mobile = phone;
            $.ajax({
                url:PC_COMMONURL+"/submitQuestion",
                type:"post",
                data:data,
                asnyc:true,
                success:function(data){
                    let json = JSON.parse(data);
                    if(json.code == SUCCESS_CODE){                        
                        alert(json.message);
                    }
                }
            })
        }
    }
}
</script>



