<template>
    <section class="row product">
        <slot></slot>
        <ul class="clearfix" v-if="productArr.length != 0">
            <li v-for="(item,index) in productArr" :key="index" class="col-xs-3">
                <a :car-id = 'item.id' @click="getcarDetail(item.id)">
                     <span class="hot" v-if="item.titleLabels == 'Hot'"></span>
                    <div class="product-item">
                        <div class="product-img">
                            <img :src="item.icon" alt="" class="img-responsive">
                        </div>
                        <div class="product-detail">
                            <div class="car-name clearfix">
                                <span>{{item.name}}</span>
                                <div class="labels pull-right" v-if="item.labels">
                                    <span class="car-label" v-for="(item,index) in computedLabels(item.labels)" :key="index">{{item}}</span>
                                </div>                           
                            </div>
                            <div class="car-desc">
                                {{item.desc}}
                            </div> 
                            <div class="car-price">
                                <p class="car-prices" v-if = 'item.nowPrice' >
                                    <strong class="now-price">现价{{item.nowPrice}}万</strong>
                                    <del class="old-price">原价{{item.primePrice}}万</del>
                                </p>
                                <p class="car-prices" v-if = 'item.firstPayment' >
                                    <del class="first-pay">首付{{item.firstPayment}}万</del>
                                    <span class="month-pay">月供{{item.monthPayment}}元</span>
                                </p>                               
                            </div>
                        </div>
                    </div>
                </a>               
            </li>
        </ul>
    </section>
</template>

<script>
export default {
    props:{
        productArr:Array,
        type:Number
    },
    methods:{
        computedLabels:function(labels){
            console.log(labels);
            return labels.split(',');
        },
        viewType:function(){
            if(this.type == 1){
                return 'leaseCarDesc'
            }else if(this.type == 2){
                return 'importCarDesc'
            }else{
                return 'secondHandCarDesc'
            }
        },
        getcarDetail(id) {
            let name = this.viewType();
            this.$router.push({
                name: name,
                params: {
                id: id
                }
            })
        }
    }
}
</script>



